@use 'utilities' as *;

.banner {
  position: relative;
  padding-top: 42px;
  padding-bottom: 20px;
  background-color: var(--color-dire-wolf);
  color: var(--color-white);
  isolation: isolate;
  @media (width >= 480px) {
    padding-bottom: 42px;
  }
  @media (width >= 768px) {
    padding-top: 67px;
    padding-bottom: 30px;
  }
  @media (width >= 1000px) {
    padding-top: 120px;
    padding-bottom: 120px;
  }
  @media (width >= 1440px) {
    padding-top: 140px;
    padding-bottom: 140px;
  }

  &::before {
    position: absolute;
    inset: 0;
    z-index: -1;
    content: '';
    background-color: hsl(0deg 0% 0% / 0.3);
  }

  &__inner {
    display: flex;
    gap: 30px;
    justify-content: space-between;

    @media (width >= 480px) {
      padding-right: calc(var(--container-padding-horizontal) + 40px);
      padding-left: calc(var(--container-padding-horizontal) + 40px);
    }
    @media (width >= 768px) {
      padding-right: calc(var(--container-padding-horizontal) + 33px);
      padding-left: calc(var(--container-padding-horizontal) + 33px);
    }
    @media (width >= 1000px) {
      padding-right: calc(var(--container-padding-horizontal) + 60px);
      padding-left: calc(var(--container-padding-horizontal) + 60px);
    }
    @media (width >= 1440px) {
      padding-right: calc(var(--container-padding-horizontal) + 80px);
      padding-left: calc(var(--container-padding-horizontal) + 80px);
    }
  }

  &__bg {
    z-index: -2;
  }

  &__btn {
    flex-shrink: 0;
    align-self: flex-start;
    max-width: 200px;
    padding: 12px 16px;
    margin-top: 26px;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--color-sugar-milk);
      }
    }
    @media (width >= 480px) {
      margin-top: 0;
    }
    @media (width >= 768px) {
      max-width: 350px;
      padding: 13px 28px;
      margin-top: 13px;
      font-size: 12px;
    }
    @media (width >= 1000px) {
      align-self: center;
      max-width: 450px;
      padding: 17px 45px;
      margin-top: 0;
      font-size: 14px;
    }
  }
}
