@use 'utilities' as *;

.reviews {
  @include adaptive-prop('padding-top', 50, 679.98, 150, 768);
  @include adaptive-prop('padding-bottom', 80, 679.98, 100, 768, 90, 1000, 86, 1440);

  &__inner {
    // this is done through variable for &__decor-line's correct positioning
    @include adaptive-var('header-max-width', 300, 679.98, 170, 680, 260, 1000, 374, 1280);
    @include adaptive-var('reviews-inner-gap', 10, 680, 16, 768, 38, 1000, 28, 1200, 18, 1440);

    position: relative;
    gap: var(--reviews-inner-gap);

    @media (width >= 680px) {
      // grid doesn't work correctly with swiper
      display: flex;
    }
  }

  &__header {
    max-width: var(--header-max-width);
    @media (width >= 680px) {
      flex-shrink: 0;
    }
  }

  &__body {
    overflow: hidden;
    @media (width >= 680px) {
      flex: 1 1 auto;
    }
  }

  &__slider {
    padding-bottom: 34px;
    text-align: center;

    @media (width >= 480px) {
      text-align: left;
    }
    @media (width >= 680px) {
      padding-bottom: 24px;
    }
    @media (width >= 1200px) {
      padding-bottom: 54px;
    }
  }

  &__decor-line {
    position: absolute;
    bottom: -50px;
    left: var(--container-padding-horizontal);

    @media (width >= 680px) {
      left: calc(var(--container-padding-horizontal) + var(--reviews-inner-gap) + var(--header-max-width));
    }
    @media (width >= 1280px) {
      left: calc(var(--container-padding-horizontal) + var(--header-max-width));
    }
  }
}

.item-review {
  padding: 0 45px;
  @include adaptive-var('item-right-max-width', 170, 680, 209, 768, 264, 1000, 372, 1280);

  @media (width >= 400px) {
    padding: 0 60px;
  }
  @media (width >= 440px) {
    padding: 0 70px;
  }
  @media (width >= 460px) {
    padding: 0 80px;
  }
  @media (width >= 480px) {
    display: grid;
    grid-template-areas:
      'video social'
      'video name'
      'video text'
      'video buy';
    grid-template-rows: auto auto 1fr auto;
    grid-template-columns: 1fr var(--item-right-max-width);
    column-gap: 13px;
    padding: 0 20px;
  }
  @media (width >= 680px) {
    padding: 0;
  }
  @media (width >= 1000px) {
    column-gap: 16px;
  }

  & > :where(*:not(:last-child)) {
    margin-bottom: 10px;
  }

  &__video-wrapper {
    max-width: 330px;
    aspect-ratio: 1 / 1;
    cursor: pointer;

    &::before {
      position: absolute;
      inset: 0;
      z-index: 1;
      content: '';
      background-color: var(--color-chocolate);
      opacity: 0;
    }

    @media (any-hover: hover) {
      &:hover {
        .item-review__video-play-icon {
          fill: var(--color-jasmine);
        }
      }
    }
    @media (width >= 480px) {
      grid-area: video;
      margin-bottom: 0;
    }
    @media (width >= 768px) {
      max-width: 420px;
    }
  }

  &__video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    width: 50px;
    height: 50px;
    content: '';
    border-radius: 50%;
    opacity: 85;
    transform: translate(-50%, -50%);

    &-icon {
      position: absolute;
      top: 50%;
      left: 50%;
      max-width: 100%;
      max-height: 100%;
      content: '';
      opacity: 0;
      fill: #bdbdbd;
      transition: fill var(--transition-sm) var(--easing-out);
      transform: translate(-50%, -50%);
    }
  }

  &__social {
    display: flex;
    gap: 8px;
    align-items: center;
    justify-content: center;

    @media (width >= 480px) {
      grid-area: social;
      justify-content: stretch;
    }

    &-link {
      @media (any-hover: hover) {
        &:hover {
          .item-review__social-icon {
            fill: var(--color-chocolate);
          }
        }
      }
    }

    &-icon {
      max-width: 15px;
      max-height: 15px;
      fill: var(--color-black);
      @media (width >= 992px) {
        max-width: 25px;
        max-height: 25px;
      }
    }
  }

  &__name {
    @media (width <= 768px) {
      font-size: 12px;
    }
    @media (width >= 480px) {
      grid-area: name;
    }
  }

  &__text {
    margin-bottom: 20px;

    @media (width >= 480px) {
      grid-area: text;
      margin-bottom: 13px;
    }
  }

  &__buy {
    display: inline-block;
    padding: 15px 20px;
    @include adaptive-prop('font-size', 8, 768, 14, 1000);
    @include adaptive-prop('padding-inline', 20, 768, 35, 1000);
    @include adaptive-prop('padding-block', 15, 768, 20, 1000);

    @media (width >= 480px) {
      grid-area: buy;
      align-self: end;
      justify-self: end;
    }
  }
}

// double selector to increase specificity to override swiper styles without !important
.swiper-pagination.swiper-pagination {
  --swiper-pagination-bottom: 0;
}

.swiper-slide-active {
  .item-review__video-play-icon {
    opacity: 1;
  }

  .item-review__video-wrapper::before {
    opacity: 0.2;
  }
}
