@use 'utilities' as *;

.qna {
  @include adaptive-prop('padding-top', 34, 679.98, 130, 768);
  @include adaptive-prop('padding-bottom', 50, 679.98, 150, 768);

  &__inner {
    @include adaptive-prop('gap', 25, 768, 45, 992, 60, 1440);
  }

  &__header {
    @include adaptive-prop('max-width', 235, 679.98, 170, 680, 200, 768, 300, 1000, 345, 1440);
  }

  &__item {
    --accordion-padding: 9px 34px 9px 6px;
    --accordion-trigger-margin-negative: -9px -34px -9px -6px;

    padding: var(--accordion-padding);
    background-color: var(--color-white);
    box-shadow: 0 4px 11px 0 rgb(196 174 151 / 0.2);

    @media (width >= 480px) {
      --accordion-padding: 12px 34px 12px 8px;
      --accordion-trigger-margin-negative: -12px -34px -12px -8px;
    }
    @media (width >= 992px) {
      --accordion-padding: 16px 34px 16px 13px;
      --accordion-trigger-margin-negative: -16px -34px -16px -13px;
    }

    &:where(:not(:last-child)) {
      margin-bottom: 10px;
    }

    &.opened {
      & .qna__question::before {
        filter: invert(1);
        transform: scaleY(-1) translateY(50%);
      }

      .qna__item-content {
        @media (width >= 480px) {
          padding-bottom: 18px;
          transform: translateY(18px);
        }
        @media (width >= 992px) {
          padding-bottom: 22px;
          transform: translateY(22px);
        }
      }
    }

    &-title {
      margin: var(--accordion-trigger-margin-negative);
    }
  }

  &__question {
    position: relative;
    display: block;
    width: 100%;
    padding: var(--accordion-padding);
    text-align: left;

    /* stylelint-disable-next-line no-descending-specificity */
    &::before {
      position: absolute;
      top: 50%;
      right: 11px;
      width: 8px;
      height: 11px;
      pointer-events: none;
      content: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSI4IiBoZWlnaHQ9IjEwIiBmaWxsPSJub25lIiB2aWV3Qm94PSIwIDAgOCAxMCI+PHBhdGggZmlsbD0iIzAwMCIgZD0ibS41IDUgLjYtLjkgMi40IDMuNFYuMmgxdjcuM2wyLjQtMy40LjYuOUw0IDkuOC41IDVaIi8+PC9zdmc+');
      transition: transform var(--transition-xsm) var(--easing-out);
      transform: translateY(-50%);
    }

    @media (any-hover: hover) {
      &::before {
        transition-property: transform, filter;
      }

      &:hover::before {
        filter: invert(1);
      }
    }
  }

  &__answer {
    line-height: 1.5;
  }
}
