@use 'utilities' as *;

.header {
  position: relative;
  z-index: var(--z-index-lg);

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include adaptive-prop-short('gap', 10, 39);
  }

  @media (width >= 992px) {
    padding-top: 4px;
  }

  &__logo-wrapper {
    display: inline-block;
    max-width: 71px;
  }

  &__phone {
    --phone-icon-size: 15px;
    @include adaptive-prop('margin-right', 23, 360, 6, 480, 0, 768);

    position: relative;
    display: flex;
    justify-content: center;
    transition: color var(--transition-sm) var(--easing-default);
    @media (any-hover: hover) {
      &:hover {
        color: var(--color-chocolate);

        .header__phone-icon {
          fill: var(--color-jasmine);
        }
      }
    }
    @media (width >= 992px) {
      --phone-icon-size: 20px;
    }
  }

  &__phone-icon {
    position: absolute;
    top: -5px;
    left: 0;
    max-width: var(--phone-icon-size);
    max-height: var(--phone-icon-size);
    fill: var(--color-sand-trap);
    transition: fill var(--transition-sm) var(--easing-default);
  }

  &__phone-number {
    padding-left: calc(var(--phone-icon-size) + 2px);
    font-size: 10px;
    text-transform: uppercase;

    @media (width >= 992px) {
      font-size: 14px;
    }
  }

  &__nav {
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    overflow: auto;
    background-color: var(--color-chocolate);
    transition: transform var(--transition-sm) var(--easing-default);
    transform: translateX(100%);

    @media (width >= 768px) {
      position: static;
      height: auto;
      overflow: visible;
      background-color: unset;
      transition: none;
      transform: none;
    }

    &.active {
      transform: translateX(0);
    }
  }

  &__ul {
    padding: 20px 17px 14px;
    @media (width >= 768px) {
      display: flex;
      flex-wrap: wrap;
      gap: 0 10px;
      align-items: center;
      padding: 0;
    }

    & li:not(:last-child) {
      margin-bottom: 10px;
      @media (width >= 768px) {
        margin-bottom: 0;
      }
    }

    & a {
      display: inline-block;
      width: 100%;
      padding: 5px 30px;
      font-size: 10px;
      line-height: 1.5;
      color: var(--color-sugar-milk);
      text-transform: uppercase;

      @media (any-hover: hover) and (width < 768px) {
        &::before {
          background-color: var(--color-sugar-milk);
        }
      }

      @media (any-hover: hover) and (width >= 768px) {
        &::before {
          background-color: var(--color-chocolate);
        }
      }

      @media (width >= 768px) {
        width: auto;
        padding: 5px 5px 3px;
        margin-bottom: 0;
        color: var(--color-black);
      }
      @media (width >= 992px) {
        font-size: 14px;
      }
    }
  }

  &__burger {
    position: relative;
    display: none;
    width: 30px;
    height: 25px;
    cursor: pointer;

    &::before,
    &::after,
    &-center-lines,
    &-center-lines::before,
    &-center-lines::after {
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      content: '';
      background-color: var(--color-chocolate);
    }

    &-center-lines,
    &-center-lines::before,
    &-center-lines::after {
      transition:
        top var(--transition-sm) var(--transition-sm) var(--easing-default),
        transform var(--transition-sm) var(--easing-default) 0s;
    }

    &-center-lines {
      top: 50%;
    }

    &::before {
      top: 0;
      transition:
        top var(--transition-sm) var(--transition-sm) var(--easing-default),
        transform var(--transition-sm) var(--easing-default);
    }

    &::after {
      bottom: 0;
      transition:
        bottom var(--transition-sm) var(--transition-sm) var(--easing-default),
        transform var(--transition-sm) var(--easing-default);
    }

    &-center-lines::before {
      top: 6px;
      height: 0.9px;
    }

    &-center-lines::after {
      top: -6px;
      height: 0.9px;
    }

    &.active {
      .header__burger-center-lines,
      .header__burger-center-lines::before,
      .header__burger-center-lines::after {
        top: 50%;
        transition:
          top var(--transition-sm) var(--easing-default),
          transform var(--transition-sm) var(--transition-sm) var(--easing-default);
        transform: scaleX(0);
      }

      &::before {
        top: 50%;
        transition:
          top var(--transition-sm) var(--easing-default),
          transform var(--transition-sm) var(--transition-sm) var(--easing-default);
        transform: rotate(45deg);
      }

      &::after {
        bottom: 50%;
        transition:
          bottom var(--transition-sm) var(--easing-default),
          transform var(--transition-sm) var(--transition-sm) var(--easing-default);
        transform: rotate(-45deg);
      }
    }

    @media (width < 768px) {
      display: inline-block;
    }
  }
}
