[data-modal] {
  position: fixed;
  top: 50%;
  left: calc(var(--vw) * 50);
  z-index: var(--z-index-xxlg);
  display: block;
  min-width: calc(var(--vw) * 60);
  max-width: calc(var(--vw) * 80);
  max-height: 95vh;
  max-height: 95dvh;
  overflow: auto;
  pointer-events: none;
  background-color: #fff;
  opacity: 0;
  transition: 0.5s ease;
  transition-property: opacity, transform;
  transform: perspective(100px) translate(-50%, -100%) scale(1.2) rotateX(5deg);
}

[data-modal]::backdrop {
  display: none;
}

[data-modal][open] {
  pointer-events: all;
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}
