@use 'utilities' as *;

.teachers {
  padding-top: 50px;
  @include adaptive-prop('padding-bottom', 50, 1000, 83, 1440);

  &__inner {
    @include adaptive-prop('gap', 0, 679.98, 10, 680, 14, 768, 38, 1000, 80, 1440);
  }

  &__header {
    @include adaptive-prop('max-width', 200, 360, 300, 480, 300, 679.98, 143, 680, 187, 768, 262, 1000, 350, 1440);
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0 20px;
    @include adaptive-prop('column-gap', 20, 360, 25, 480, 10, 768, 20, 1000, 75, 1440);
    @include adaptive-prop('row-gap', 20, 360, 30, 480, 30, 1000, 78, 1440);

    @media (width >= 400px) {
      padding: 0 25px;
    }
    @media (width >= 440px) {
      padding: 0 50px;
    }
    @media (width >= 480px) {
      grid-template-columns: 1fr 1fr;
      padding: 0;
    }
  }
}

.item-teacher {
  display: flex;
  align-items: flex-end;

  &__image-wrapper {
    flex: 1 1 auto;
    aspect-ratio: 21 / 22;
  }

  &__content {
    z-index: 1;
    width: min-content;
    margin-bottom: 8px;
    margin-left: -60px;
    color: var(--color-white);
    text-align: center;
    background-color: var(--color-chocolate);
    @include adaptive-prop('padding-inline', 8, 360, 6, 480, 6, 768, 9, 1000);
    @include adaptive-prop('padding-block', 15, 360, 10, 480, 10, 768, 15, 1000);
    @include adaptive-prop('min-width', 150, 360, 190, 479.98, 115, 480, 115, 679.98, 106, 680, 110, 768, 160, 1000, 170, 1440);

    @media (width >= 480px) and (width < 1000px) {
      margin-bottom: 5.5px;
      margin-left: -40px;
    }

    & > :where(*:not(:last-child)) {
      margin-bottom: 3px;
    }
  }

  &__name {
    font-family: var(--font-family-title);
    @include adaptive-prop('font-size', 11, 479.98, 8, 480, 8, 768, 12.5, 1000);
  }

  &__description {
    @include adaptive-prop('font-size', 7.5, 768, 11, 1000);
  }
}
