@use 'utilities' as *;

.title {
  font-family: var(--font-family-title);
  line-height: 1.11;
  @include adaptive-prop('font-size', 15, 360, 15, 768, 40, 1000);
}

.title-sm {
  font-family: var(--font-family-title);
  line-height: 1.11;
  @include adaptive-prop('font-size', 10, 360, 12, 480, 12, 768, 16, 1000);
}

.section-text-lg {
  @include adaptive-prop('font-size', 10, 768, 14, 1000);
}
