@use 'utilities' as *;

.steps {
  @include adaptive-prop('padding-top', 30, 480, 130, 768);
  @include adaptive-prop('padding-bottom', 82, 768, 102, 1000);

  &__inner {
    @media (width >= 640px) {
      gap: 10px;
    }
    @media (width >= 992px) {
      gap: 25px;
    }
    @media (width >= 1280px) {
      gap: 21px;
    }
  }

  &__header {
    @include adaptive-prop('max-width', 204, 360, 300, 480, 300, 679.98, 150, 680, 192, 768, 300, 1000, 372, 1440);
  }

  &__text {
    @media (width <= 480px) {
      font-size: 8px;
    }
  }

  &__list {
    padding: 0 2px 0 3px;
    @include adaptive-prop('column-gap', 25, 360, 40, 480, 35, 768, 35, 1000, 25, 1440);
    @include adaptive-prop('row-gap', 20, 360, 25, 480, 27, 768, 37, 1000, 72, 1440);
    @media (width >= 480px) {
      padding: 0 10px;
    }
    @media (width >= 640px) {
      padding: 0 0 0 3px;
    }
    @media (width >= 1000px) {
      padding-left: 0;
    }
  }

  &__decor-line {
    bottom: -52px;
    @media (width >= 480px) {
      bottom: -56px;
    }
    @media (width >= 768px) {
      bottom: -52px;
    }
    @media (width >= 1000px) {
      bottom: -72px;
    }
    @media (width >= 1440px) {
      left: -19px;
    }
  }
}

.candle-step {
  display: grid;
  grid-template-rows: 1fr auto;
  grid-template-columns: auto 1fr;
  row-gap: 15px;
  @include adaptive-prop('column-gap', 16, 360, 21, 480, 21, 768, 32, 1000, 39, 1440);

  &:nth-child(4n),
  &:nth-child(4n - 1) {
    grid-template-columns: 1fr auto;

    & .candle-step__image-wrapper {
      grid-column: 1 / 2;
    }
  }

  &__text {
    @include adaptive-prop('max-width', 73, 360, 94, 480, 94, 768, 141, 1000, 172, 1440);
    @include adaptive-prop('font-size', 6, 360, 8, 480, 8, 768, 14, 1000);
  }

  &__num {
    font-family: var(--font-family-title);
    color: var(--color-chocolate);
    // opacity: 0.71;
    @include adaptive-prop('font-size', 12, 360, 20, 480, 20, 768, 30, 1000);
  }

  /* stylelint-disable-next-line no-descending-specificity */
  &__image-wrapper {
    grid-row: 1 / -1;
    grid-column: 2 / 3;
    aspect-ratio: 2 / 3;
    @media (width >= 480px) {
      aspect-ratio: 85 / 150;
    }
    @media (width >= 1100px) {
      aspect-ratio: 160 / 220;
    }
  }
}
