/* stylelint-disable scss/comment-no-empty */
// * --------------------------------------------------------------------------------------------- *
// * ========================================  VARIABLES  ======================================== *
// * --------------------------------------------------------------------------------------------- *

:root {
  // font-family
  --font-family: comfortaa, 'Lucida Sans', georgia, sans-serif;
  --font-family-title: 'Yeseva One', Verdana, Geneva, sans-serif;

  // color palette
  --color-jasmine: #ffbd59;
  --color-sand-trap: #bea495;
  --color-stoneware: #887163;
  --color-chocolate: #7b6152;
  --color-sugar-milk: #fff8f3;
  --color-snowflake: #fafafa;
  --color-dire-wolf: #272727;
  --color-black: #000000;
  --color-white: #fff;
  --color-tangled-web: #b3b3b3;

  // transitions
  --transition-xsm: 0.2s;
  --transition-sm: 0.3s;
  --transition-md: 0.5s;
  --transition-lg: 0.8s;
  --transition-xlg: 1s;

  --transition-delay-xsm: 0.05s;
  --transition-delay-sm: 0.1s;
  --transition-delay-md: 0.15s;
  --transition-delay-lg: 0.2s;
  --transition-delay-xlg: 0.3s;

  @media (prefers-reduced-motion: reduce) {
    --transition-xsm: 0s;
    --transition-sm: 0s;
    --transition-md: 0s;
    --transition-lg: 0s;
    --transition-xlg: 0s;
    --transition-delay-xsm: 0s;
    --transition-delay-sm: 0s;
    --transition-delay-md: 0s;
    --transition-delay-lg: 0s;
    --transition-delay-xlg: 0s;
  }

  // easing
  --easing-default: ease;
  --easing-out: ease-out;
  --easing-linear: linear;
  --easing-bounce: cubic-bezier(0.68, -0.55, 0.265, 1.55);

  // z-index
  --z-index-md: 10;
  --z-index-lg: 20;
  --z-index-xlg: 30;
  --z-index-xxlg: 40;
  --z-index-xxxlg: 50;
  --z-index-top-layer: 9999999999;

  // container that limits the width of the section
  --container-padding-horizontal: 10px;

  @media (width >= 640px) {
    --container-padding-horizontal: 64px;
  }
  @media (width >= 992px) {
    --container-padding-horizontal: 20px;
  }
  // if you change this, you should change also $media-container-max-width to be equal to this value
  --container-max-width: calc(1160px + 2 * var(--container-padding-horizontal));
}

$media-container-max-width: 1200px;
