// double selector to increase specificity to override swiper styles without !important
.swiper {
  min-width: 0;
  min-height: 0;
}

.swiper-pagination.swiper-pagination {
  --swiper-pagination-bullet-horizontal-gap: 2px;
  --swiper-pagination-bottom: 0;
  --swiper-pagination-bullet-width: 28px;
  --swiper-pagination-bullet-height: 4px;
  --swiper-pagination-bullet-border-radius: 0;
  --swiper-pagination-color: var(--color-chocolate);
  --swiper-pagination-bullet-inactive-color: var(--color-sand-trap);
  --swiper-pagination-bullet-active-color: var(--color-chocolate);
  --swiper-pagination-bullet-inactive-opacity: 1;

  & .swiper-pagination-bullet {
    transition: background var(--transition-sm) var(--easing-out);
  }
}
