.document-lock-overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: var(--z-index-xlg);
  width: 120%;
  height: 120%;
  pointer-events: none;
  background-color: hsl(0deg 0% 0% / 0.5);
  opacity: 0;
  transition: opacity 0.5s ease;
  transform: translate(-50%, -50%);
}

html.locked {
  // overflow: hidden; both on html & on body because otherwise it won't work on some versions of iphone & ipad
  padding-right: var(--scrollbar-width);

  & .document-lock-overlay {
    pointer-events: all;
    opacity: 1;
  }
}

html.locked,
html.locked body {
  position: relative;
  overflow: hidden;
  touch-action: none;
}
