@use 'utilities' as *;

.goods {
  padding-top: 48px;
  padding-bottom: 80px;
  @media (width >= 680px) {
    padding-top: 145px;
  }
  @media (width >= 1440px) {
    padding-bottom: 100px;
  }

  &__inner {
    @include adaptive-prop('gap', 24, 640, 30, 768, 20, 1000, 69, 1440);
  }

  &__header {
    @include adaptive-prop('max-width', 235, 360, 300, 480, 300, 679.98, 180, 680, 200, 768, 300, 1000, 372, 1440);
  }

  &__text {
    @media (width <= 480px) {
      font-size: 8px;
    }
  }

  &__content {
    @include adaptive-prop-short('padding-right', 0, 48, 1000, 1440);
  }

  &__list {
    @include adaptive-prop('column-gap', 7, 360, 16, 480, 16, 768, 67, 1000, 93, 1440);
    @include adaptive-prop('row-gap', 41, 360, 30, 480, 30, 768, 67, 1000, 50, 1440);
  }

  &__decor-line {
    bottom: -50px;

    @media (width >= 640px) {
      left: -14px;
    }
    @media (width >= 1440px) {
      bottom: -70px;
      left: -69px;
    }
  }
}

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  & > :where(*:not(:last-child)) {
    margin-bottom: 10px;
  }

  &__image-wrapper {
    width: 100%;
    max-width: calc(100% - 8px);
    aspect-ratio: 273 / 310;
    margin: 0 auto;
    margin-bottom: 4.5px;
    overflow: hidden;

    @media (width >= 480px) {
      margin-bottom: 5.5px;
    }
    @media (width >= 992px) {
      max-width: calc(100% - 16px);
      margin-bottom: 8px;
    }
    @media (any-hover: hover) {
      &:hover img {
        transform: scale(1.06);
      }
    }

    & img {
      transition: transform var(--transition-sm) var(--easing-out);
    }
  }

  &__price {
    margin-bottom: 5px;
    @media (width >= 480px) {
      margin-bottom: 6.7px;
    }
    @media (width >= 992px) {
      margin-bottom: 12px;
    }
  }

  &__name {
    margin-bottom: 7px;
    @media (width >= 480px) {
      margin-bottom: 9.4px;
    }
    @media (width >= 992px) {
      margin-bottom: 16px;
    }
  }

  &__description {
    flex: 1 1 auto;
    padding: 0 4px;
    margin-bottom: 16.5px;
    @include adaptive-prop('font-size', 6, 360, 8, 480, 8, 768, 12, 1000);
    @media (width >= 480px) {
      margin-bottom: 18.5px;
    }
    @media (width >= 640px) {
      padding: 0;
    }
    @media (width >= 992px) {
      margin-bottom: 27px;
    }

    & dt,
    dd {
      display: inline;
    }
  }
}
