@use 'utilities' as *;

.btn {
  position: relative;
  display: inline-block;
  padding: 10px 21px;
  overflow: hidden;
  font-size: 8px;
  font-weight: 600;
  color: var(--color-dire-wolf);
  text-align: center;
  background-color: var(--color-jasmine);

  @media (width >= 480px) {
    padding: 12px 26px;
    font-size: 12px;
  }

  @media (width >= 1000px) {
    padding: 18px 45px;
    font-size: 14px;
  }

  @media (any-hover: hover) {
    border: 1px solid transparent;
    transition: var(--transition-sm) var(--easing-out);
    transition-property: background-color, border;

    &::before {
      position: absolute;
      top: 50%;
      left: -100%;
      width: 100%;
      height: 30px;
      content: '';
      background-color: var(--color-sugar-milk);
      transition: var(--transition-sm) var(--easing-out);
      transition-property: transform, opacity;
      transform: translate(0, -50%) rotate(-45deg);
    }

    &:hover {
      background-color: transparent;
      border: 1px solid var(--color-chocolate);
      transition-delay: 0.1s;

      &::before {
        opacity: 0;
        transform: translate(200%, -50%) rotate(-45deg);
      }
    }
  }
}
