@use 'utilities' as *;

:where(.descendant-link-hover-line a) {
  @media (any-hover: hover) {
    position: relative;

    &::before {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      content: '';
      background-color: var(--color-chocolate);
      transition: transform var(--transition-sm) var(--easing-default);
      transform: scaleX(0);
      transform-origin: right;
    }

    &:hover::before {
      transform: scaleX(1);
      transform-origin: left;
    }
  }
}
