@use 'utilities' as *;

.courses {
  @include adaptive-prop('padding-top', 30, 480, 130, 768);
  @include adaptive-prop('padding-bottom', 110, 480, 110, 680, 230, 768);

  &__inner {
    @include adaptive-prop('gap', 14, 840, 31, 1000, 74, 1440);
  }

  &__header {
    @include adaptive-prop('max-width', 205, 360, 300, 480, 300, 679.98, 143, 680, 200, 768, 300, 1000, 369, 1440);
  }

  &__body {
    padding: 0 56px 0 44px;

    @media (width >= 400px) {
      padding: 0 74px 0 62px;
    }
    @media (width >= 440px) {
      padding: 0 94px 0 82px;
    }
    @media (width >= 480px) {
      padding: 0 30px 0 14px;
    }
    @media (width >= 680px) {
      padding: 0;
    }
    @media (width >= 992px) {
      padding-right: 12px;
    }
    @media (width >= 1440px) {
      padding-right: 54px;
    }
  }

  &__list {
    @include adaptive-prop('gap', 30, 680, 36, 768, 75, 1000, 127, 1440);
    @media (width <= 480px) {
      & > :where(*:not(:last-child)) {
        margin-bottom: 30px;
      }
    }
    @media (width >= 480px) {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
    @media (width >= 940px) {
      row-gap: 65px;
    }
  }

  &__decor-line {
    bottom: -50px;
    left: -44px;
    @media (width >= 400px) {
      left: -62px;
    }
    @media (width >= 440px) {
      left: -82px;
    }
    @media (width >= 480px) {
      left: -14px;
    }
    @media (width >= 680px) {
      left: -20px;
    }
    @media (width >= 768px) {
      bottom: -70px;
    }
    @media (width >= 1000px) {
      left: -9px;
    }
    @media (width >= 1440px) {
      left: -69px;
    }
  }
}

.item-course {
  text-align: center;

  & > :where(*:not(:last-child)) {
    margin-bottom: 15px;
  }

  &__title {
    margin-bottom: 10px;

    @media (width >= 480px) {
      margin-bottom: 5px;
    }
    @media (width >= 1000px) {
      margin-bottom: 15px;
    }
    @media (width <= 768px) {
      font-size: 12px;
    }
  }

  &__description {
    padding: 0 4px;
    margin-bottom: 16px;

    @media (width >= 480px) {
      padding: 0;
      margin-bottom: 10.5px;
    }
    @media (width >= 1000px) {
      margin-bottom: 23.5px;
    }
  }

  &__btn {
    padding: 13px 16px;
    font-size: 12px;

    @media (width >= 1000px) {
      font-size: 14px;
    }
  }
}

.course-image {
  display: block;
  aspect-ratio: 244 / 333;
  margin-bottom: 30px;
  font-family: var(--font-family-title);
  font-size: 10px;
  color: var(--color-white);
  text-align: left;

  @media (width >= 480px) {
    margin-bottom: 26px;
    font-size: 9px;
  }
  @media (width >= 840px) {
    font-size: 11px;
  }
  @media (width >= 1000px) {
    margin-bottom: 28px;
  }
  @media (any-hover: hover) {
    &:hover img {
      transform: scale(1.06);
    }
  }

  & img {
    transition: transform var(--transition-sm) var(--easing-out);
  }

  &__overflow-limiter {
    position: absolute;
    inset: 0;
    overflow: hidden;
  }

  &__info--center-left {
    position: absolute;
    top: calc(50% - 20px);
    left: 0;
    z-index: 1;
    max-width: 90%;

    & > * {
      background-color: var(--color-stoneware);
    }
  }

  &__info--bottom-right {
    position: absolute;
    right: -12px;
    bottom: -12px;
    z-index: 1;
    max-width: 40%;
  }

  &__title {
    display: inline-block;
    min-width: 70%;
    max-width: 100%;
    padding: 15px 30px 15px 10px;
    margin-bottom: 15px;
    @media (width >= 480px) and (width < 840px) {
      padding: 10px 20px 10px 7px;
    }
  }

  &__study-time {
    display: inline-block;
    min-width: 31%;
    max-width: 75%;
    padding: 8px 16px 8px 10px;
    @media (width >= 480px) and (width < 840px) {
      padding: 5px 8px 5px 7px;
    }

    &-num {
      font-family: var(--font-family);
      font-size: 10px;
      @media (width >= 480px) and (width < 840px) {
        font-size: 8px;
      }
    }
  }

  &__price {
    padding: 18px 9px 18px 10px;
    background-color: var(--color-stoneware);
    @media (width >= 480px) and (width < 840px) {
      padding: 14px 7px;
    }
  }
}
