.grid-section-inner {
  @media (width >= 680px) {
    display: grid;
    grid-template-columns: auto 1fr;
  }
}

.grid-header {
  max-width: 235px;
  margin-inline: auto;
  margin-bottom: 25px;
  text-align: center;
  @media (width >= 480px) {
    margin-bottom: 30px;
  }
  @media (width >= 680px) {
    margin-inline: 0;
    margin-bottom: 0;
    text-align: left;
  }

  & > :where(*:not(:last-child)) {
    margin-bottom: 10px;
    @media (width >= 480px) {
      margin-bottom: 15px;
    }
  }
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
