@use 'utilities' as *;

.write-input {
  position: relative;
  padding: 10px 14px 18px 7px;
  margin-bottom: 7px;
  color: var(--color-chocolate);
  border: 1px solid transparent;
  border-bottom-color: var(--color-black);
  transition: var(--transition-sm) var(--easing-default);
  transition-property: border, border-radius, color;
  @include adaptive-prop('font-size', 8, 768, 14, 1000);

  &::placeholder {
    color: inherit;
  }

  &:focus {
    color: var(--color-black);
    border-bottom-color: var(--color-jasmine);
    border-left-color: var(--color-jasmine);
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
  }

  @media (width >= 992px) {
    padding: 11px 14px 11px 7px;
    margin-bottom: 13px;
  }
}
