@use 'utilities' as *;

.certificates {
  @include adaptive-prop('padding-top', 30, 680, 120, 768);
  @include adaptive-prop('padding-bottom', 80, 768, 100, 1000, 86, 1440);

  &__inner {
    // this is done through variable for &__decor-line's correct positioning
    @include adaptive-var('header-max-width', 195, 679.98, 170, 680, 195, 768, 260, 1000, 371, 1280);
    @include adaptive-var('reviews-inner-gap', 8, 768, 18, 1000, 28, 1200, 20, 1440);

    @media (width >= 480px) {
      padding-inline: 35px;
    }
    @media (width >= 680px) {
      display: flex;
      padding-right: 0;
      padding-left: var(--container-padding-horizontal);
    }

    position: relative;
    gap: var(--reviews-inner-gap);
  }

  &__header {
    max-width: var(--header-max-width);
  }

  &__slider {
    @media (width >= 680px) {
      flex: 1 1 auto;
    }

    @media (width >= $media-container-max-width) {
      margin-right: calc((100vw - var(--container-max-width)) / -2);
    }
    @media (width >= 1800px) {
      margin-right: calc((1800px - var(--container-max-width)) / -2);
    }
  }

  &__list {
    padding-bottom: 24px;

    @media (width >= 1440px) {
      padding-bottom: 50px;
    }
  }

  &__decor-line {
    position: absolute;
    bottom: -50px;
    left: var(--container-padding-horizontal);

    @media (width >= 680px) {
      left: calc(var(--container-padding-horizontal) + var(--reviews-inner-gap) + var(--header-max-width));
    }
    @media (width >= 1280px) {
      left: calc(var(--container-padding-horizontal) + var(--header-max-width));
    }
  }
}

.item-certificate {
  padding-inline: 70px;
  @media (width >= 480px) {
    padding: 0;
  }

  &__image-wrapper {
    aspect-ratio: 200 / 128;
  }
}
