.accordion {
  &__trigger {
    cursor: pointer;
    transition: var(--transition-xsm) var(--easing-out);
    transition-property: color, background;

    @media (any-hover: hover) {
      // for increased specificity
      &.accordion__trigger.accordion__trigger:hover {
        color: var(--color-sugar-milk);
        background-color: var(--color-chocolate);
      }
    }
  }

  &__content {
    display: grid;
    grid-template-rows: 0fr;
    transition: var(--transition-sm) var(--easing-default);
    transition-delay: 0s, 0.3s;
    transition-property: grid-template-rows, transform;

    & > * {
      overflow: hidden;
    }
  }

  &__item.opened {
    & .accordion__content {
      grid-template-rows: 1fr;
      padding-bottom: 15px;

      // prettier-ignore
      transition-delay: 0s, -0.3s;
      transform: translateY(15px);
    }

    & .accordion__trigger {
      color: var(--color-white);
      background-color: var(--color-sand-trap);
    }
  }
}
