@use 'utilities' as *;

.subscribe {
  &__inner {
    padding-bottom: 50px;
    @include adaptive-prop('padding-top', 50, 679.98, 150, 768);
    @include adaptive-prop('gap', 15, 768, 30, 1000, 50, 1440);

    @media (width >= 680px) {
      display: flex;
      align-items: center;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    background-color: var(--color-white);
    box-shadow: 0 4px 11px 0 rgb(199 175 165 / 0.3);
    @include adaptive-prop('padding-block', 30, 768, 75, 1000);
    @include adaptive-prop('padding-inline', 21, 1000, 42, 1440);

    @media (width >= 680px) {
      @include adaptive-prop('min-width', 370, 680, 440, 768, 592, 1000, 664, 1200);
    }
  }

  &__title {
    @include adaptive-prop('margin-bottom', 33, 768, 62, 1000);
  }

  &__input {
    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &__btn {
    align-self: flex-start;
    padding: 15px 13px;
    font-size: 8px;
    @include adaptive-prop('margin-top', 30, 768, 42, 1000, 52, 1440);

    @media (width >= 680px) {
      padding: 17px 34px;
      font-size: 14px;
    }
  }

  &__image-wrapper {
    display: none;

    @media (width >= 680px) {
      display: block;
      flex: 1 1 auto;
      aspect-ratio: 187 / 285;
      @media (width >= 1100px) {
        aspect-ratio: 448 / 604;
      }
    }
  }
}
