@use 'utilities' as *;

.reset-focus:focus {
  z-index: auto;
  outline: none;
  box-shadow: none;
}

.restore-focus:focus {
  z-index: var(--z-index-xxxlg);
  outline: 3px dashed #000;
  box-shadow: 0 0 0 3px #fff;
}

.video-onclick-loader {
  &.loading {
    background: url('/icon/_shared/video-preloader.svg') no-repeat center center;
    background-size: 100px 100px;
  }

  display: flex;
  align-items: center;
  justify-content: center;

  & iframe {
    z-index: 1;
    width: 100%;
    height: 100%;
  }
}

.decor-line {
  position: relative;
  display: block;
  width: calc(1000% - 20px + var(--container-padding-horizontal));
  height: 1px;
  background-color: var(--color-chocolate);
  transform: translateX(20px);

  &::before {
    position: absolute;
    top: -21px;
    left: -20px;
    width: 24px;
    height: 33px;
    content: url('/img/main-page/decor-line.svg');
  }
}

.section-darker {
  color: var(--color-white);
  background-color: var(--color-stoneware);

  & .decor-line {
    background-color: var(--color-tangled-web);

    &::before {
      content: url('/img/main-page/decor-line-light.svg');
    }
  }
}

.ibg {
  position: relative;
}

.ibg img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.img-cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
}

.visible-on-focus:focus-visible {
  position: relative;
  width: initial;
  height: initial;
  overflow: initial;
  clip: initial;
  clip-path: initial;
  white-space: initial;
}