/* stylelint-disable declaration-block-no-duplicate-properties */
/* stylelint-disable property-no-vendor-prefix */

/*
* -------------------------------------------------------------------------------------------- *
* ======================================  RESET STYLES  ====================================== *
* -------------------------------------------------------------------------------------------- *
*/

@use 'utilities' as *;

*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  border: none;
}

:where(
    audio,
    button,
    canvas,
    details,
    iframe,
    input,
    select,
    summary,
    textarea,
    video,
    [accesskey],
    [contenteditable],
    [href],
    [tabindex]
  ):focus-visible {
  position: relative;
  z-index: var(--z-index-top-layer);
  outline: 3px dashed #000;
  box-shadow: 0 0 0 3px #fff;
}

:where(
    input[type='text'],
    input[type='email'],
    input[type='password'],
    input[type='number'],
    input[type='search'],
    input[type='tel'],
    input[type='url'],
    input[type='datetime'],
    select,
    textarea
  ):focus {
  z-index: auto;
  outline: none;
  box-shadow: none;
}

html,
body {
  box-sizing: border-box;
  width: 100%;
  min-height: 100vh;
  min-height: 100dvh;
  padding: 0;
  margin: 0;
  line-height: 1;
  scroll-behavior: smooth;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

body {
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: stretch;
  min-height: 100vh;
  min-height: 100dvh;
  overflow: hidden;
}

.main {
  flex: 1 1 auto;
}

a {
  font: inherit;
  color: inherit;
  touch-action: manipulation;
}

a:active {
  outline: none;
}

select {
  border-radius: 0;
}

:where(a, a:active, a:visited) {
  text-decoration: none;
}

:where(ul, li) {
  list-style: none;
}

:where(img, picture, video, canvas, svg) {
  max-width: 100%;
  vertical-align: top;
}

:where(h1, h2, h3, h4, h5, h6) {
  font: inherit;
  overflow-wrap: break-word;
}

:where(input, button, textarea, select) {
  font: inherit;
  font-size: 100%;
}

:where(input[type='checkbox'], input[type='checkbox'] + label, input[type='radio'], input[type='radio'] + label) {
  cursor: pointer;
}

svg {
  width: 100%;
}

textarea {
  resize: vertical;
}

button {
  color: inherit;
  -webkit-appearance: button;
  appearance: button;
  touch-action: manipulation;
  cursor: pointer;
  background-color: transparent;

  :where(&:disabled, &[aria-disabled]) {
    pointer-events: none;
    cursor: default;
  }
}

dialog {
  inset: unset;
  width: auto;
  max-width: none;
  height: auto;
  max-height: none;
  border: none;
}

.noscript {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 10px;
  font-size: 18px;
  color: #fff;
  text-align: center;
  background-color: tomato;
}

mark {
  color: inherit;
}

em {
  font-style: normal;
}

small {
  font-size: 80%;
}

:where(pre, code, kbd, samp) {
  font-family: monospace;
  font-size: 1em;
}

:where(sub, sup) {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

progress {
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

blockquote,
q {
  quotes: none;
}

/* stylelint-disable-next-line selector-pseudo-element-no-unknown */
input::ms-clear {
  display: none;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

:where(
    input[type='search']::-webkit-search-decoration,
    input[type='search']::-webkit-search-cancel-button,
    input[type='search']::-webkit-search-results-button,
    input[type='search']::-webkit-search-results-decoration
  ) {
  margin-left: 7px;
}

//  Correct the cursor style of increment and decrement buttons in Chrome.

:where([type='number']::-webkit-inner-spin-button, [type='number']::-webkit-outer-spin-button) {
  height: auto;
}

// 1. Correct the odd appearance in Chrome and Safari.
// 2. Correct the outline style in Safari.
// 3. Remove the inner padding in Chrome and Safari on macOS.

[type='search'] {
  -webkit-appearance: textfield; // 1
  appearance: textfield; // 1
  outline-offset: -2px; // 2

  &::-webkit-search-decoration {
    -webkit-appearance: none; // 3
  }
}

// 1. Correct the inability to style clickable types in iOS and Safari.
// 2. Change font properties to `inherit` in Safari.

::-webkit-file-upload-button {
  font: inherit; // 2
  -webkit-appearance: button; // 1
}

// Add the correct display in Edge and Firefox.

details {
  display: block;
}

// Add the correct display in all browsers.

summary {
  display: list-item;
}

/*
* ====================================  design-specific  ==================================== *
*/

/* stylelint-disable-next-line no-duplicate-selectors */
body {
  font-family: var(--font-family);
  font-size: 14px;
  line-height: 1.14;
  color: var(--color-black);
  background-color: var(--color-sugar-milk);
}

:where(html, body) {
  @media (any-hover: hover) and (width >= 768px) {
    scrollbar-color: var(--color-chocolate) var(--color-sand-trap);
    scrollbar-width: thin;

    &::-webkit-scrollbar {
      width: 7px;
      height: 7px;
    }

    &::-webkit-scrollbar-track {
      outline: 1px solid var(--color-chocolate);
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--color-sand-trap);
      outline: 1px solid var(--color-sand-trap);

      &:hover {
        background-color: var(--color-chocolate);
      }

      &:active {
        background-color: var(--color-dire-wolf);
      }
    }
  }
}
