@use 'utilities' as *;

.hero {
  position: relative;
  @include adaptive-prop('padding-top', 61, 360, 110, 480, 130, 768, 123, 1000, 194, 1440);
  @include adaptive-prop('padding-bottom', 50, 360, 130, 480, 130, 767.98, 274, 768, 238, 1000, 420, 1440);

  &__body {
    position: relative;
    z-index: var(--z-index-md);
    @include adaptive-prop('max-width', 203, 360, 180, 480, 230, 640, 230, 768, 333, 992, 380, 1080);

    & > :where(*:not(:last-child)) {
      margin-bottom: 15px;

      @media (width >= 640px) {
        margin-bottom: 20px;
      }
      @media (width >= 992px) {
        margin-bottom: 15px;
      }
    }
  }

  &__title {
    line-height: 1.2;
    @include adaptive-prop('font-size', 18, 360, 15, 480, 20, 640, 20, 768, 30, 1000);
  }

  &__text {
    @include adaptive-prop('max-width', 167, 480, 500, 768, 300, 992, 999, 1080);
    @media (width >= 480px) {
      line-height: 1.5;
    }
    @media (width >= 992px) {
      margin-bottom: 30px;
      line-height: inherit;
    }
    @media (width >= 1080px) {
      margin-bottom: 48px;
    }
  }

  &__btn {
    @include adaptive-prop('padding-block', 11, 360, 15, 640, 15, 768, 20, 1000);
    @include adaptive-prop('padding-inline', 23, 360, 30, 640, 30, 768, 45, 1000);
    @include adaptive-prop-short('font-size', 10.5, 14, 360, 640);

    @media (width >= 640px) {
      font-size: 14px;
    }
  }

  &__image-wrapper {
    position: absolute;
    top: 31px;
    right: 0;
    max-width: min(calc(100% - 203px), 125px);
    @media (width >= 360px) {
      max-width: min(calc(100% - 203px), 300px);
    }
    @media (width >= 480px) {
      max-width: min(calc(100% - 180px), 300px);
    }
    @media (width >= 640px) {
      max-width: min(calc(100% - 230px), 350px);
    }
    @media (width >= 768px) {
      top: 19px;
      max-width: min(calc(100% - 150px), 616px);
    }
    @media (width >= 992px) {
      top: 5px;
      max-width: calc(100% - 300px);
    }
    @media (width >= 1440px) {
      top: 19px;
      max-width: min(calc(100% - 380px), 1060px);
    }
    @media (width >= 1800px) {
      right: calc((100% - 1800px) / 2);
    }
  }
}
