@use 'utilities' as *;

.about {
  @include adaptive-prop('padding-bottom', 39, 360, 50, 480, 110, 768);
  @include adaptive-prop-short('padding-top', 25, 40, 360, 480);
  @media (width >= 1000px) {
    padding-top: 65px;
  }
  @media (width >= 1440px) {
    padding-top: 50px;
  }

  &__inner {
    display: flex;
    align-items: center;
    @include adaptive-prop('gap', 25, 360, 17, 480, 48, 768, 20, 1000, 110, 1440);
    @media (width >= 480px) {
      padding-right: 0;
    }
  }

  &__left {
    @include adaptive-prop('max-width', 180, 320, 234, 360, 250, 479.98, 172, 480, 200, 768, 300, 1000, 370, 1440);

    & > :where(*:not(:last-child)) {
      margin-bottom: 10px;
      @media (width >= 480px) {
        margin-bottom: 20px;
      }

      @media (width >= 1000px) {
        margin-bottom: 15px;
      }
    }
    @media (width >= 1440px) {
      margin-top: 9px;
    }
  }

  &__text {
    @media (width <= 480px) {
      font-size: 8px;
    }

    & p {
      margin-bottom: 15px;
    }
  }

  &__right {
    position: relative;
    flex: 1 1 auto;
    @media (width >= 1000px) {
      transform: translateY(-15px);
    }
    @media (width >= 1440px) {
      transform: translateY(0);
    }
  }

  &__images-wrapper {
    min-height: 100px;
    @media (width >= 480px) {
      min-height: max-content;
      padding-bottom: 10px;
      margin-top: -29px;
      overflow-x: auto;
      scrollbar-color: var(--color-chocolate) var(--color-sand-trap);
      scrollbar-width: thin;

      &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
      }

      &::-webkit-scrollbar-track {
        outline: 1px solid var(--color-chocolate);
      }

      &::-webkit-scrollbar-thumb {
        background-color: var(--color-sand-trap);
        outline: 1px solid var(--color-sand-trap);
      }
    }
    @media (width >= 768px) {
      margin-top: 25px;
    }
    @media (width >= 1000px) {
      margin-top: 0;
    }
    @media (width >= $media-container-max-width) {
      margin-right: calc((100vw - var(--container-max-width)) / -2);
    }
    @media (width >= 1800px) {
      margin-right: calc((1800px - var(--container-max-width)) / -2);
    }
  }

  &__images {
    @include adaptive-prop('gap', 5, 480, 10, 768, 20, 1000);
    @media (width >= 480px) {
      --about-images-offset: 30px;

      display: flex;
      align-items: flex-end;
      justify-content: center;
      margin-right: calc(max(480px + var(--about-images-offset) - 100vw, 0px) * -1);
    }
    @media (width >= 768px) {
      align-items: center;
    }

    /* 838px is 1000px - var(--about-images-offset), if you change this var, you change 838px (may be another location in this file) */
    @media (width >= 838px) {
      --about-images-offset: 162px;

      margin-right: calc(max(100vw - 1000px + var(--about-images-offset), 0px) * -1);
    }
    @media (width >= 1000px) {
      margin-right: calc(max(1000px + var(--about-images-offset) - 100vw, 0px) * -1);
    }

    @media (width >= $media-container-max-width) {
      margin-right: 0;
    }
  }

  &__figure {
    @media (width >= 480px) {
      flex: 1 1 26.37%;
    }

    @media (width >= 768px) {
      transform: translateY(7px);
    }

    @media (width >= 1000px) {
      transform: translateY(0);
    }

    &:nth-child(n + 2) {
      display: none;
      @media (width >= 480px) {
        display: block;
      }
    }
  }

  &__image-wrapper {
    aspect-ratio: 1 / 1;
  }

  &__figure-center {
    @media (width >= 480px) {
      flex: 1 1 42.85%;
    }

    @media (width >= 768px) {
      transform: translateY(0);
    }

    & .about__image-wrapper {
      overflow: hidden;
      border-radius: 50% 50% 0% 0% / 50% 50% 0% 0%;
    }
  }

  &__image-caption {
    display: none;
    @include adaptive-prop-short('font-size', 5, 11, 768, 1000);
    @media (width >= 768px) {
      display: block;
      margin-top: 5px;
    }
    @media (width >= 992px) {
      display: block;
      margin-top: 10px;
    }
  }

  &__decor-line {
    position: absolute;
    right: 0;
    bottom: -32px;
    width: 100%;
    transform: translateX(12px);
    @media (width >= 480px) {
      bottom: -50px;
    }
    @media (width >= 768px) {
      bottom: -19px;
      width: calc(100% + 12px);
    }
    @media (width >= 838px) {
      bottom: -50px;
    }
    @media (width >= $media-container-max-width) {
      --right-offset: calc((100vw - var(--container-max-width)) / -2);

      right: var(--right-offset);
      width: calc(100% - var(--right-offset) + 12px);
    }
    @media (width >= 1440px) {
      width: calc(100% - var(--right-offset) + 100px);
    }
  }
}
