@use 'utilities' as *;

.footer {
  &__inner {
    --logo-wrapper-max-width: 71px;
    --inner-gap: 46px;

    display: flex;
    flex-direction: column;
    gap: var(--inner-gap);
    align-items: center;
    padding-block: 50px;
    font-size: 14px;
    text-align: center;

    @media (width >= 680px) {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
    @media (width >= 768px) {
      justify-content: space-between;
      font-size: 10px;
    }
    @media (width >= 992px) {
      flex-wrap: nowrap;
      font-size: 14px;
      text-align: left;
    }
  }

  &__logo-wrapper {
    display: inline-block;
    max-width: var(--logo-wrapper-max-width);
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 14px;
    width: 100%;

    @media (width >= 680px) {
      flex: 1 1 auto;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
    }
    @media (width >= 768px) {
      justify-content: flex-end;
      width: auto;
      max-width: calc(100% - var(--logo-wrapper-max-width) - var(--inner-gap));
    }
    @media (width >= 992px) {
      justify-content: center;
    }

    & li {
      @media (width < 680px) {
        width: 100%;
      }
    }

    & a {
      display: inline-block;
      padding: 4px;
      color: #333;
      text-transform: uppercase;

      @media (width < 680px) {
        position: relative;

        &::after {
          position: absolute;
          top: 0;
          left: 50%;
          width: calc(100vw - var(--container-padding-horizontal) * 2);
          width: calc(100dvw - var(--container-padding-horizontal) * 2);
          height: 100%;
          content: '';
          transform: translateX(-50%);
        }
      }
    }
  }

  &__misc {
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    width: 100%;

    @media (width >= 992px) {
      align-items: flex-start;
      width: auto;
      max-width: 300px;
    }
  }

  &__misc-item {
    transition: color var(--transition-sm) var(--easing-default);

    @media (any-hover: hover) {
      &:hover {
        color: var(--color-chocolate);
      }
    }
  }

  &__social {
    display: flex;
    flex-wrap: wrap;
    gap: 7px;
    align-items: center;
    justify-content: center;
    margin-top: -4px;

    & a {
      display: inline-block;
      padding: 4px;
      @media (any-hover: hover) {
        &:hover {
          & svg {
            filter: invert(50%);
          }
        }
      }
    }

    & svg {
      max-width: 22px;
      max-height: 22px;
    }
  }
}
